<template>
  <div id="app">
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
    <mt-popup v-model="popupVisible" :modal="false" class="subpage" position="right" pop-transition="popup-fade">
      <!-- <mt-header title="二级页面" class="boder-header">
        <mt-button @click="hidePopup" icon="back" slot="left"></mt-button>
      </mt-header> -->
      <router-view name="subpage"></router-view>
    </mt-popup>
    <mt-tabbar v-model="tabSelected" fixed >
      <mt-tab-item id="home">
        <img slot="icon" :src="iconUrl.home">首页
      </mt-tab-item>
      <mt-tab-item id="invite" ref="inviteBtn">
        <img slot="icon" :src="iconUrl.invite">邀请
      </mt-tab-item>
      <mt-tab-item id="mine">
        <img slot="icon" :src="iconUrl.mine">我的
      </mt-tab-item>
    </mt-tabbar>
    <invite-cover :showCover="showCover" @bindHideInviteCover="bindHideInviteCover"></invite-cover>
  </div>
</template>

<script>
import {mapActions} from 'vuex'
import 'swiper/dist/css/swiper.css'
import inviteCover from '@/components/common/InviteCover'
// icon 图标
import index from '@/assets/images/index.png'
import index2 from '@/assets/images/index2.png'

import invite from '@/assets/images/invite.png'
import invite2 from '@/assets/images/invite2.png'

import mine from '@/assets/images/mine.png'
import mine2 from '@/assets/images/mine2.png'

export default {
  name: 'App',
  data () {
    return {
      value: 'mine',
      tabSelected: 0,
      popupVisible: false,
      showCover: false,
      iconUrl: {
        home: index,
        invite: invite2,
        mine: mine2
      }
    }
  },
  components: {
    inviteCover
  },
  methods: {
    bindHideInviteCover () {
      this.showCover = false
    },
    ...mapActions(['getUserInfo']),
    changeStoreRoute (val) {
      switch (val.path) {
        case '/':
          this.tabSelected = 'home'
          this.activeTab(0)
          break
        case '/user/usercenter':
          this.tabSelected = 'mine'
          this.activeTab(2)
          break
      }

      /** 显示、隐藏子页面 */
      if (val.meta.subpage) {
        this.popupVisible = true
      } else {
        this.popupVisible = false
      }
    },
    hidePopup () {
      showSubpage = false
    },
    activeTab (tab) {
      if (tab === 0) {
        this.iconUrl.invite = invite2
        this.iconUrl.home = index
        this.iconUrl.mine = mine2
      } else if (tab === 1) {
        this.iconUrl.invite = invite
        this.iconUrl.home = index2
        this.iconUrl.mine = mine2
      } else if (tab === 2) {
        this.iconUrl.invite = invite2
        this.iconUrl.home = index2
        this.iconUrl.mine = mine
      }
    }
  },
  watch: {
    $route: 'changeStoreRoute',
    tabSelected: function (val, oldVal) {
      switch (val) {
        case 'home':
          this.activeTab(0)
          this.showCover = false
          this.$router.push('/')
          break
        case 'invite':
          this.$toast('敬请期待')
          // if (!sessionStorage.isLogin) {
          //   this.$toast('未登录')
          //   this.$router.push('/user/usercenter')
          // }
          this.activeTab(1)
          // this.showCover = true
          break
        case 'mine':
          this.activeTab(2)
          this.showCover = false
          this.$router.push('/user/usercenter')
          break
      }
    }
  },
  created () {
    // 每次加载请求数据
    this.getUserInfo()

    // 监听事件
    this.$bus.on('FloatTool:invite', (val) => {
      this.showCover = true
    })
  },
  mounted () {
  }
}
</script>

<style lang="scss">
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.mint-tabbar>.mint-tab-item.is-selected{
  background-color: #fafafa!important;
  color: #f77437!important;
  text-align: center!important;
}
.subpage {
  width: 100%;
  height: 100%;
}

img {
  /* 图片兼容性处理 */
  content: normal !important;
  pointer-events: none;
}

html,
body {
  /* 去滚动条 */
  overflow: scroll;
  overflow-x: hidden;
  /* -webkit-overflow-scrolling: touch; */
  user-select: none;
  background: white;
  min-height: 100%;
}
body{
  padding-top: constant(safe-area-inset-top);
  padding-bottom: constant(safe-area-inset-bottom);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

@font-face {
  font-family: "dinm";
  src: url("/static/font/DINMittelschrift.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

a {
  text-decoration: none;
}

#app {
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB",
    "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.bodyRel {
  position: relative;
}
.bodyFixed {
  position: fixed;
  width: 100%;
}

/*bankbanner*/
.bank-banner {
  background-color: none;
  & .swiper-container {
    & .swiper-pagination {
      & .swiper-pagination-bullet {
        width: 10px;
        height: 5px;
        background-color: #6b3ae3;
        border-radius: 1rem;
      }
    }
  }
}
.border-bottom {
  background-image: linear-gradient(
    180deg,
    #e6e6e6,
    #e6e6e6 50%,
    transparent 50%
  );
  background-repeat: no-repeat;
  background-size: 120% 1px;
  background-position: 0 100%;
}
.border-right {
  background-image: linear-gradient(
    90deg,
    #e6e6e6,
    #e6e6e6 50%,
    transparent 50%
  );
  background-repeat: no-repeat;
  background-size: 1px 100%;
  background-position: right top;
}
.single-image img {
  vertical-align: middle;
}
</style>



