<template>
    <div class="invite-conver" @click="bindHideCover" v-show="showCover">
        <img src="../../assets/images/new_arrow1_bg@3x.png" alt="">
    </div>
</template>
<script>
    export default {
      props: ['showCover'],
      methods: {
        bindHideCover () {
          this.$emit('bindHideInviteCover')
        }
      }
    }
</script>
<style scoped lang="scss">
.invite-conver{
    position: fixed;
    top:0;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,.5);
    text-align: center;
    z-index: 99;
    img{
        margin-left:35px ;
        width: 276px;
        height: 208px;
    }
}
</style>



