import http from './http'

export default function () {
  // 请求配置微信sdk
  var url = location.href.split('#')[0]
  http.postData('/wechat/getJsApi', {url: url}).then(res => {
    var wxApi = res.body.content.jsApi
    wxShare(wxApi)
  }).catch(err => {
    console.log(err)
  })
  // http.get('http://wechatbg.wty168.com/mp/Search/share/mid/10.html', {url: url}).then(res => {
  //   var res = JSON.parse(res) || {}
  //   if (res.code === 200) {
  //     wxShare(res.data)
  //   }
  // }).catch(err => {
  //   console.log(err)
  // })

  function wxShare (options) {
    console.log(options)
    wx.config({
      debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
      appId: options.appId, // 必填，公众号的唯一标识
      timestamp: options.timestamp, // 必填，生成签名的时间戳
      nonceStr: options.noncestr, // 必填，生成签名的随机串
      signature: options.signature, // 必填，签名
      jsApiList: ['onMenuShareAppMessage', 'onMenuShareTimeline', 'onMenuShareQQ', 'onMenuShareQZone'] // 必填，需要使用的JS接口列表
    })
    wx.ready(function () {
      console.log('调用sdk成功')

      // 微信分享
      // var title = [
      //   '和我一起组队拆红包，名额有限，快上车!',
      //   '我在财喵星球开了两个现金红包了，点击和我一起开红包>>',
      //   '送你一个现金红包，先到先得，再不领就没有啦！',
      //   '还差一个人就能拆红包了，来帮我完成任务，我们一起分钱~'
      // ]

      // function randomNum () {
      //   return Math.floor(Math.random() * title.length)
      // }

      var title = '大额信用卡免费在线申请，3天下卡！'
      var img = `http://h5.caimiaowanka.com/activity/static/images/share-icon.jpg`
      var desc = '办信用卡还能领500元现金哦>>'
      var link = `http://h5.caimiaowanka.com/activity/index.html?invite_uid=${localStorage.invite_id}`

      // 分享朋友
      wx.onMenuShareAppMessage({
        title: title, // 分享标题
        desc: desc, // 分享描述
        link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: img, // 分享图标
        type: '', // 分享类型,music、video或link，不填默认为link
        dataUrl: '' // 如果type是music或video，则要提供数据链接，默认为空
      })

      // 分享朋友圈

      wx.onMenuShareTimeline({
        title: title, // 分享标题
        link: link, // 分享链接，该链接域名或路径必须与当前页面对应的公众号JS安全域名一致
        imgUrl: img // 分享图标
      })
      // 分享qq
      wx.onMenuShareQQ({
        title: title, // 分享标题
        desc: desc, // 分享描述
        link: link, // 分享链接
        imgUrl: img // 分享图标
      })
      // qq空间分享
      wx.onMenuShareQZone({
        title: title, // 分享标题
        desc: desc, // 分享描述
        link: link, // 分享链接
        imgUrl: img // 分享图标
      })
    })
  }
}



