import fly from 'flyio'
import qs from 'qs'
import md5 from 'md5'
import UUID from './uuid'

import { Toast } from 'mint-ui'

/** flyio 配置 */
fly.config.headers = { 'content-type': 'application/json' }
fly.config.timeout = 10000

if (process.env.NODE_ENV === 'production') {
  // fly.config.baseURL = 'https://prod.wdjzt88.com'
  // fly.config.baseURL = 'http://th5.caimiaowanka.com/'
  fly.config.baseURL = 'https://carta.caimiaowanka.com'
} else {
  fly.config.baseURL = 'https://carta.caimiaowanka.com'
  // fly.config.baseURL = 'http://th5.caimiaowanka.com/'
  // fly.config.baseURL = 'https://dev.wdjzt88.com'
}

/**
 * 后端接口方法
 * status
 */
class Http {
  constructor () {
    this.msgSendContent = {
      'body': {
        'content': {},
        'head': {
          'OSVersion': 'A7.1.1',
          'appVersion': '10.0.6',
          'channel': 'weixin',
          'clientModel': 'weixinh5',
          'device_type': 'weixinh5',
          'packageName': 'com.caimiao.wanka',
          'uuid': this.getUUId()
        }
      },
      // 这里的key是session_id
      'key': localStorage.getItem('session_id') || md5(new Date().getTime())
    }
  }

  getUUId () {
    var uid = localStorage.getItem('uuid')
    if (uid) {
      return uid
    } else {
      localStorage.setItem('uuid', UUID.random())
    }
  }

  /** 请求错误处理 */
  errorHandle (err) {
    return err
  }

  /** 获取短信验证码 */
  getMsgCode (phone, code) {
    return fly.get('/app/Captcha/web', { phone: phone, type: 3, imgcode: code })
      .then((res) => {
        return res && res.data
      })
      .catch((err) => {
        return this.errorHandle(err)
      })
  }

  /** 获取图形验证码 */
  getImageCode (phone) {
    return fly.get('/app/imagecode/index3', { phone: phone })
      .then((res) => {
        return res && res.data
      })
      .catch((err) => {
        return this.errorHandle(err)
      })
  }

  /** 重置密码 */
  resetPassword (param) {
    return this.postData('/app/user/password', param)
      .then((res) => {
        if (res.body) return res.body
        else {
          Toast(res.body.statusMsg)
          return null
        }
      })
  }

  /** 登录请求 */
  toLogin (param) {
    return this.postData('/app/login', param)
      .then((res) => {
        if (res.body.content) return res.body.content
        else {
          Toast(res.body.statusMsg)
          return null
        }
      })
      .catch((err) => {
        return this.errorHandle(err)
      })
  }
  getUserInfo () {
    return this.postData('/app/user/info')
      .then((res) => {
        return res
      }).catch((err) => {
        return this.errorHandle(err)
      })
  }

  // 修改昵称
  modifyNickname (params) {
    return this.postData('/app/user/exit', params)
      .then((res) => {
        return res
      }).catch((err) => {
        return this.errorHandle(err)
      })
  }
  // 根据卡号 查询银行名称
  queryBankName (params) {
    return this.postData('/app/user/queryBankName', params)
      .then((res) => {
        return res
      }).catch((err) => {
        return this.errorHandle(err)
      })
  }

  // 绑定银行卡
  bindBankCard (params) {
    return this.postData('/app/user/bank/', params)
      .then((res) => {
        return res
      }).catch((err) => {
        return this.errorHandle(err)
      })
  }
  get (url, data) {
    return fly.get(url, data, {headers: {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}}).then(res => {
      if (res && res.data) {
        return res.data
      } else {
        return null
      }
    }).catch(err => {
      Toast('网络错误')
      return err
    })
  }

  post (url, data) {
    return fly.post(url, JSON.stringify(data)).then(res => {
      if (res && res.data) {
        return res.data
      } else {
        return null
      }
    }).catch(err => {
      Toast('请检查网络')
      return err
    })
  }

  postData (url, data) {
    if (data) {
      this.msgSendContent.body.content = data
    }
    let sendData = this.msgSendContent
    return fly.post(url, JSON.stringify(sendData), { headers: { 'Content-Type': 'application/json' } })
      .then((res) => {
        // TODO 这里seesion_id 以及出错处理等都需要加
        if (res && res.data && res.data.session_id) {
          localStorage.setItem('session_id', res.data.session_id)
        }
        return res && res.data
      })
      .catch((err) => {
        Toast('请检查网络')
        return this.errorHandle(err)
      })
  }
}

const http = new Http()

export default http



