// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import router from './router'
import store from './store'
import http from './modules/http'
import App from './App'
import Toast from './components/common/Toast'
import VueBus from 'vue-bus'
import shareData from './modules/shareData'
import utils from './modules/utils'

import VueLazyload from 'vue-lazyload'
// import Loading from './components/common/Loading'
// import Alert from './components/common/Alert'
// import Confirm from './components/common/Confirm'
import {
  Button,
  Header,
  Tabbar,
  TabItem,
  TabContainer,
  TabContainerItem,
  Navbar,
  Field,
  Loadmore,
  Checklist,
  MessageBox,
  Popup,
  InfiniteScroll,
  Cell
} from 'mint-ui'
import 'mint-ui/lib/style.css'
import '@/assets/css/iconfont.css'
import '@/assets/css/mint_reset.css'

if (utils.isWeixin()) {
  shareData()
}

Vue.config.productionTip = false
Vue.prototype.$http = http

/** 引入的插件列表 */
// Vue.use(VueLazyload, {
//   preLoad: 1.3,
//   error: '/static/images/index.png',
//   loading: '/static/images/index.png',
//   attempt: 1
// })
Vue.use(VueLazyload)
Vue.use(VueBus)
Vue.use(Toast)
Vue.use(InfiniteScroll)
// Vue.use(Loading)
// Vue.use(Alert)
// Vue.use(Confirm)

Vue.component(Button.name, Button)
Vue.component(Header.name, Header)
Vue.component(Tabbar.name, Tabbar)
Vue.component(TabItem.name, TabItem)
Vue.component(TabContainer.name, TabContainer)
Vue.component(TabContainerItem.name, TabContainerItem)
Vue.component(Navbar.name, Navbar)
Vue.component(Field.name, Field)
Vue.component(Checklist.name, Checklist)
Vue.component(Loadmore.name, Loadmore)
Vue.component(Popup.name, Popup)
Vue.component(Tabbar.name, Tabbar)
Vue.component(TabItem.name, TabItem)
Vue.component(Cell.name, Cell)

router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth) {
    if (sessionStorage.getItem('isLogin')) {
      next()
    } else {
      next({
        path: '/user/login',
        query: { redirected: to.fullPath }
      })
    }
  } else {
    next()
  }
})

Vue.filter('moneyFormal', function (value) {
  if (!value || value === 0) return '0'
  value = parseInt(value) / 100
  value += ''
  var reg = value.indexOf('.') > -1 ? /(\d{1,3})(?=(?:\d{3})+\.)/g : /(\d{1,3})(?=(?:\d{3})+$)/g
  value = value.replace(reg, '$1,')
  return value
})
/** filter */
Vue.directive('title', {
  inserted: function (el, binding) {
    document.title = binding.value
  }
})

Vue.filter('moneyFormal', function (value) {
  if (!value || value === 0) return '0'
  value = parseInt(value) / 100 // 分到元
  value += '' // 转成字符串
  var reg = value.indexOf('.') > -1 ? /(\d{1,3})(?=(?:\d{3})+\.)/g : /(\d{1,3})(?=(?:\d{3})+$)/g // 千分符的正则
  value = value.replace(reg, '$1,') // 千分位格式化
  return value
})

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



