import Vue from 'vue'
import Router from 'vue-router'

const login = r => require.ensure([], () => r(require('@/pages/login/Login')), 'login')
const agreement = r => require.ensure([], () => r(require('@/pages/login/LoginAgreement')), 'agreement')

// index
const CreditCardIndex = r => require.ensure([], () => r(require('@/pages/index/CreditCardIndex')), 'CreditCardIndex')
const BankInfo = r => require.ensure([], () => r(require('@/pages/index/BankInfo')), 'bankInfo')

// webview
const Webview = r => require.ensure([], () => r(require('@/pages/webview/Webview')), 'Webview')

// 个人中心
const UserCenter = r => require.ensure([], () => r(require('@/pages/mine/Usercenter')), 'UserCenter')

Vue.use(Router)

export default new Router({
  mode: 'hash',
  routes: [
    {
      name: 'agreement',
      path: '/user/agreement',
      components: {
        subpage: agreement
      },
      meta: {
        subpage: true
      }
    },
    {
      path: '/user/login',
      name: 'login',
      components: {
        subpage: login
      },
      meta: {
        subpage: true
      }
    },
    {
      name: 'usercenter',
      path: '/user/usercenter',
      component: UserCenter,
      meta: {
        keepAlive: true,
        requireAuth: true
      }
    },
    {
      path: '/',
      name: 'CreditCardIndex',
      meta: {
        title: '首页',
        keepAlive: true
      },
      component: CreditCardIndex
    },
    {
      path: '/bankInfo',
      name: 'BankInfo',
      meta: {
        title: '银行详情',
        keepAlive: false, // 不需要缓存
        subpage: true
      },
      components: {
        subpage: BankInfo
      }
    },
    {
      path: '/webview',
      name: 'webview',
      components: {
        subpage: Webview
      },
      meta: {
        subpage: true
      }
    }
  ]
})



